@import "../../assets//styles/veriables.scss";

.guarantees {
  padding: 30px 0;
  // .guarantees__row
  &__row {
    display: flex;
    flex-wrap: wrap;
    row-gap: 15px;
    padding: 10px 0;
    margin: 30px -10px 0 -10px;
  }
  &__container {
    padding: 0 20px;
  }
  // .guarantees__column
  &__column {
    flex: 0 1 50%;
    color: #ffffff;
    padding: 0 10px;
    display: flex;
  }
}
.item-guarantees {
  border-radius: 10px;
  padding: 20px;
  background: #333333;
  width: 100%;
  display: flex;
  align-items: center;
  column-gap: 15px;
  // .item-guarantees__img
  &__img {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 70px;
    flex: 0 0 70px;
    background: $orange;
    border-radius: 50%;
    border: 2px solid #ffffff;
    img {
      width: 100%;
      max-width: 40px;
    }
  }
  // .item-guarantees__body
  &__body {
    display: flex;
    flex-direction: column;
    row-gap: 10px;
    height: 100%;
    padding: 10px 0;
  }
  // .item-guarantees__title
  &__title {
    font-size: 18px;
    flex: 1 1 auto;
  }
  // .item-guarantees__text
  &__text {
  }
}

@media (max-width: $mobile) {
  .guarantees__row {
    row-gap: 10px;
  }
  .guarantees__column {
    flex: 0 1 100%;
  }
}