@import "../../assets/styles/veriables.scss";

.catalog {
  padding: 120px 0;
  position: relative;

  &::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: #333333d8;
    z-index: 0;
  }

  // .catalog__container
  &__container {
    position: relative;
    z-index: 1;
  }

  // .catalog__body
  &__body {
    margin-top: 60px;
  }

  .css-t3ipsp-control {
    box-shadow: none !important;
    border-color: #333333 !important;
  }
  .css-1nmdiq5-menu {
    .css-1n6sfyn-MenuList .css-tr4s17-option {
      background-color: $orange;
    }
    .css-d7l1ni-option {
      background-color: #33333357;
    }
  }

  &._kg {
    background: url("../../assets/img/catalog/kg.jpg") center / cover no-repeat;
    background-attachment: fixed;
  }
  &._kr {
    background: url("../../assets/img/catalog/kr.jpg") center / cover no-repeat;
    background-attachment: fixed;
  }

  label {
    display: inline-block;
    margin: 0 0 7px 0;
  }
}

.filter-catalog {
  display: flex;
  column-gap: 10px;
  margin-top: 20px;
  align-items: center;
  // .filter-catalog__item
  &__item {
    max-width: 180px;
    width: 100%;
    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }
  &__search {
    padding: 10px 20px;
    color: #fff;
    background-color: $orange;
    border-radius: 8px;
    font-size: 20px;
    transition: all 0.3s ease 0s;
    height: 38px;
    display: flex;
    align-items: center;

    @media (any-hover: hover) {
      &:hover {
        background: #fc6d00;
      }
    }
  }
  .select-field {
    &__label {
      color: #ffffff;
      font-size: 17px;
    }
  }
}

.country-catalog {
  display: flex;
  column-gap: 10px;

  &__title {
    font-size: 25px;
    color: $orange;
    margin-right: 20px;
  }
  &__item {
    cursor: pointer;
    padding: 10px;
    border-radius: 10px;
    background: #fff;
    font-weight: 600;
    transition: all 0.3s ease 0s;
    margin-right: 10px;
    &._active {
      transform: scale(1.1);
      color: #ffffff;
      background: $orange;
    }
  }
}
.catalog-items-quantity {
  margin-top: 20px;
  color: #ffffff;
  background: $orange;
  padding: 10px;
  display: inline-block;
  border-radius: 10px;
  font-size: 17px;
  &._loading {
    opacity: 0.4;
  }
}
.select-field__error,
.text-field__error {
  position: absolute;
  transform: translate(5px, 5px);
  color: red;
}

@media (max-width: $tablet) {
  .catalog {
    padding: 70px 0;
  }
  .filter-catalog {
    flex-wrap: wrap;
    row-gap: 10px;
    &__item {
      flex: 1 1 25%;
      max-width: 100%;

      &_search {
        flex: 1 1 100%;
      }
    }
  }
}
@media (max-width: $mobileSmall) {
  .country-catalog__title {
    font-size: 17px;
  }
  .filter-catalog {
    &__item {
      flex: 1 1 33.333%;
      &_year {
        flex: 1 1 100%;
      }
    }
    &__search {
      width: 100%;
      display: flex;
      justify-content: center;
    }
    
  }
  .catalog-items-quantity  {
    width: 100%;
    text-align: center;
  }
  .select-field__error,
.text-field__error {
  position: absolute;
  transform: translate(0px, 5px);
  color: red;
}
}
