@import "../../../assets/styles/veriables.scss";

.search-result {
  margin-top: 50px;
  // .search-result__cars
  &__cars {
  }
}
.cars-search-result {
  // .cars-search-result__row
  &__row {
    display: flex;
    flex-wrap: wrap;
    row-gap: 30px;
    margin: 0 -10px;
  }
  // .cars-search-result__column
  &__column {
    display: flex;
    padding: 0 10px;
    flex: 0 1 25%;
  }
  // .cars-search-result__item
  &__item {
  }
}
.item-cars-search-result {
  background: #333333e4;
  padding: 20px;
  border-radius: 10px;
  width: 100%;
  display: flex;
  flex-direction: column;

  box-shadow: 3px 3px 20px #ffffff25;
  // .item-cars-search-result__img
  &__img {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 185px;
    img {
      border-radius: 10px;
      width: 100%;
      height: 100%;
      max-width: 250px;
      object-fit: cover;
    }
  }
  // .item-cars-search-result__body
  &__body {
    margin-top: 15px;
    flex: 1 1 auto;
    color: #ffffff;
  }
  // .item-cars-search-result__name
  &__name {
    color: $orange;
    font-size: 17px;
  }
  // .item-cars-search-result__price
  &__price {
    margin-top: 10px;
    font-size: 17px;
  }

  // .item-cars-search-result__description
  &__description {
    margin-top: 13px;
    line-height: 16px;
  }
  // .item-cars-search-result__buttons
  &__buttons {
    margin-top: 30px;
    color: #ffffff;
  }
  // .item-cars-search-result__btn
  &__btn {
    display: flex;
    width: 100%;
    color: #ffffff;
    justify-content: center;
    
    &:not(:last-child) {
      margin-bottom: 10px;
    }
    background: $orange;
    padding: 10px 7px;
    border-radius: 13px;
    text-align: center;
    font-size: 16px;
    transition: all 0.3s ease 0s;
    @media (any-hover: hover) {
      &:hover {
        cursor: pointer;
        background: #f1a344;
      }
    }
  }
}

.pagination-search-result {
  margin-top: 40px;

  // .pagination-search-result__item
  &__item {
    background: $orange;
    padding: 10px 12px;
    border-radius: 13px;
    text-align: center;
    font-size: 16px;
    color: #ffffff;
    width: 80px;
    display: inline-block;
    // .pagination-search-result__item_prev
    &_prev {
    }
    // .pagination-search-result__item_next
    &_next {
    }
    &.pagination-search-result__current-page {
      width: auto;
      height: 100%;
      margin: 0 12px;
    }
  }
}

.empty-search-result {
  background: #d5d5d524;
  color: $orange;
  padding: 20px;
  border-radius: 10px;
  font-size: 22px;
  font-weight: 600;
  margin-top: 30px;
}
.img-skeleton {
  background: #d5d5d524;
  width: 217px;
  height: 162.75px;

  display: flex;
  flex-direction: column;
  row-gap: 3px;
  justify-content: center;
  align-items: center;
  font-weight: 600;
  text-transform: uppercase;

  border-radius: 10px;
  &::before {
    content: "Trading";
    color: $orange;
    font-size: 30px;
  }
  &::after {
    content: "car";
    color: #ffffff;
    font-size: 18px;
  }
}

@media (max-width: $tablet) {
  .cars-search-result__column {
    flex: 0 1 33.333%;
    padding: 0 5px;
  }
}
@media (max-width: $mobile) { 
  .pagination-search-result {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .cars-search-result__column {
    flex: 0 1 50%;
  }
  .item-cars-search-result__img {
    img {
      max-width: 100%;
    }
  }
}
@media (max-width: $mobileSmall) { 
  .cars-search-result__column {
    flex: 0 1 100%;
  }
}