@import "../../../assets/styles/veriables.scss";

.skeleton-wrapper {
  padding: 0 10px;
  flex: 0 1 25%;
}
.skeleton {
  // max-width: 295px;
  width: 100%;
  padding: 20px;
  background: #333333e4;
  box-shadow: 3px 3px 20px #ffffff25;
  animation: loading 0.7s ease-in-out infinite alternate running 0s none;
  border-radius: 10px;
  height: 443.5px;
  // .skeleton__img
  &__img {
    border-radius: 10px;
    width: 100%;

    height: 100%;
    max-height: 187.5px;
    background: #d5d5d524;

    display: flex;
    flex-direction: column;
    row-gap: 3px;
    justify-content: center;
    align-items: center;
    font-weight: 600;
    text-transform: uppercase;

    &::before {
      content: "Trading";
      color: $orange;
      font-size: 30px;
    }
    &::after {
      content: "car";
      color: #ffffff;
      font-size: 18px;
    }
  }
  // .skeleton__body
  &__body {
    margin-top: 15px;
  }
  // .skeleton__name
  &__name {
    height: 15px;
    background: #d5d5d524;
    border-radius: 7px;
  }
  // .skeleton__price
  &__price {
    margin-top: 10px;
    height: 15px;
    background: #d5d5d524;
    border-radius: 7px;
  }
  // .skeleton__description
  &__description {
    margin-top: 13px;
    height: 40px;
    background: #d5d5d524;
    border-radius: 10px;
  }
  // .skeleton__buttons
  &__buttons {
    margin: 25px 0 0 0;
  }
  // .skeleton__btn
  &__btn {
    height: 36px;
    background: #d5d5d524;
    border-radius: 10px;
    &:not(:last-child) {
      margin: 0 0 10px 0;
    }
  }
}

@keyframes loading {
  100% {
    opacity: 0.5;
  }
}

@media (max-width: $tablet) {
  .skeleton-wrapper {
    flex: 0 1 33.333%;
  }
}

@media (max-width: $mobile) {
  .skeleton-wrapper {
    flex: 0 1 50%;
  }
}

@media (max-width: $mobileSmall) {
  .skeleton-wrapper {
    flex: 0 1 100%;
  }
}
